import React from "react";
import { Formik, Form } from "formik";
import * as Yup from "yup";
import { TextField } from "./../components/TextField";
import { useNavigate } from "react-router-dom";
import "./OrderForm.css";
import customerService from "./../services/customerService";
import orderService from "./../services/orderService";
import { useSelector } from "react-redux";
export const OrderForm = () => {
  const navigate = useNavigate();
  const phoneRegExp = /^[+]*[(]{0,1}[0-9]{1,4}[)]{0,1}[-\s\./0-9]*$/;
  const total = useSelector((state) => state.cart.totalCost);
  const validate = Yup.object({
    Cus_ID: Yup.number().required("Bắt buộc"),
    firstName: Yup.string().max(40, "Ít hơn 40 ký tự").required("Bắt buộc"),
    lastName: Yup.string().max(10, "Ít hơn 10 ký tự").required("Bắt buộc"),

    email: Yup.string()
      .email("Email không đúng định dạng")
      .required("Bắt buộc"),
    phone: Yup.string()
      .required("Bắt buộc")
      .matches(phoneRegExp, "Số điện thoại không đúng định dạng"),

    address: Yup.string().required("Bắt buộc"),
  });
  const handleSubmit = (data) => {
    // customerService.add(data).then((res) => {
    //   if (res.errorCode === 0) {
    //     console.log("Success");
    //     let today = new Date();
    //     let date =
    //       today.getFullYear() +
    //       "-" +
    //       (today.getMonth() + 1) +
    //       "-" +
    //       today.getDate();
    //     let time =
    //       today.getHours() +
    //       ":" +
    //       today.getMinutes() +
    //       ":" +
    //       today.getSeconds();
    //     let orderDate = date + " " + time;
    //     const id = res.data.Cus_ID;
    //     const order = { cusID: id, date: orderDate, totalCost: total };
    //     orderService.add(order).then((res) => {
    //       if (res.errorCode === 0) {
    //         navigate(`/orderdetail/${res.data.Or_ID}`);
    //       }
    //     });
    //   }
    // });
    localStorage.setItem("customer", JSON.stringify({ customer: data }));
    navigate(`/orderdetail`);
  };

  return (
    <Formik
      initialValues={{
        Cus_ID: 0,
        firstName: "",
        lastName: "",
        email: "",
        phone: "",
        address: "",
      }}
      validationSchema={validate}
      onSubmit={(values) => {
        handleSubmit(values);
      }}
    >
      {(formik) => (
        <div>
          <div className="container">
            <h1 className="text-center mt-3">Đơn đặt hàng</h1>
            <Form className="text-dark fw-bold ">
              <div className="row">
                <div className="col-sm-6">
                  {" "}
                  <TextField
                    label="Họ và tên"
                    name="firstName"
                    type="text"
                    placeholder="Nhập vào họ và tên đệm"
                  />
                </div>
                <div className="col-sm-6 mt-1">
                  {" "}
                  <TextField
                    label=""
                    name="lastName"
                    type="text"
                    placeholder="Nhập tên"
                  />
                </div>
              </div>

              <TextField
                label="Email"
                name="email"
                type="email"
                placeholder="Nhập vào địa chỉ email"
              />
              <TextField
                label="Số điện thoại"
                name="phone"
                type="tel"
                placeholder="Nhập số điện thoại"
              />
              <TextField
                label="Địa chỉ"
                name="address"
                type="text"
                placeholder="Nhập vào địa chỉ"
              />
              <TextField
                label="Ghi chú"
                name="note"
                type="text"
                placeholder="Để lại ghi chú (nếu có)...."
                rows="3"
              />
              <div className="text-center mb-3">
                {" "}
                <button className="btn btn-danger " type="reset">
                  Quay về
                </button>
                <button
                  className="btn btn-dark ms-3"
                  type="button"
                  onClick={formik.handleSubmit}
                >
                  Mua Hàng
                </button>
              </div>
            </Form>
          </div>
        </div>
      )}
    </Formik>
  );
};
export default OrderForm;
